code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: Abril;
  src: url('./fonts/AbrilFatface-Regular.ttf');
}

@font-face {
  font-family: Sora;
  src: url('./fonts/Sora-VariableFont_wght.ttf');
}

.flex-column {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.flex-row {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}